.SongListItem {
  display: flex;
  padding: 7px;
  border-radius: 20px;
}

.SongListItem.selected {
  background-color: rgb(29, 87, 87);
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 0 32px 0 rgba(31, 38, 135, 0.37);
}

.songCover {
  width: 55px;
  height: 55px;
  filter: drop-shadow(0px 6px 20px rgba(0, 0, 0, 0.15));
  border-radius: 15px;
}

.songInfo {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: left;
  padding-left: 10px;
}

.songTitle {
  font-weight: bold;
  font-size: 20px;
}

.songArtist {
  font-weight: 500;
  font-size: 14px;
  color: #a10f0f;
}
